<template>
<div class="switch-button" :class="{'active': value}">
  <div class="ball-box" @click="change_fn">
    <div class="ball"></div>
  </div> {{label}}
</div>
</template>

<script>
/** 父層帶入 options 參數說明
    value      [boolean]    預設值 true or false
    callback   [function]   切換後執行的callback
*/
export default {
  props: ['options'],
  data () {
    return {
      value: (this.options.value)? this.options.value : false,
      label: (this.options.label)? this.options.label : ''
    }
  },
  methods: {
    change_fn() {
      this.value = !this.value;
      if (typeof this.options.callback === 'function') {
        this.options.callback(this.value);
      }
    }
  }
}
</script>

<style lang="scss" scoped>
    @import './style.scss';
</style>


