<template>
  <div>

    <article>
      <h4>Vue-cli 框架套用 fontawesome v4.7.0</h4>
      <i class="fa fa-check" aria-hidden="true"></i>
      <i class="fa fa-calculator" aria-hidden="true"></i>
      <i class="fa fa-diamond" aria-hidden="true"></i>
      <i class="fa fa-desktop" aria-hidden="true"></i>
      <i class="fa fa-eye" aria-hidden="true"></i>
      <i class="fa fa-gamepad" aria-hidden="true"></i>
    </article>

    <article>
      <h4>html 表單元素</h4>
      <input type="text" class="test" />

      <button>button</button>

      <select>
          <option value="">--</option>
          <option value="11">11</option>
          <option value="22">22</option>
      </select>

      <label>
          <input type="checkbox" >
          <span>checkbox</span>
      </label>

      <label>
          <input type="radio" name="test" value="A" >
          <span>radio A</span>
      </label>

      <label>
          <input type="radio" name="test" value="B" >
          <span>radio B</span>
      </label>
      <br>
      <textarea style="width: 99%;"></textarea>

    </article>

    <article>
      <h4>呼叫ajax</h4>
      當下ajax數量: {{$root.ajaxCount}} <br>
      <button @click="test_sendOne">Send one</button> 
      <button @click="test_sendMore">Send More</button> 
    </article>

    <article>
      <h4>Switch Button</h4>
      <SwitchButton :options="switchButton"></SwitchButton> 
    </article>

    <article>
      <h4>slider bar</h4>
      <button @click="sliderBarStart">啟用</button>
        <button @click="sliderBarStop">停用</button>
        <input type="text" v-model="sliderBar.options.result" />
        <br><br>
        <SloderBar v-if="sliderBar.isShow"
          :options="sliderBar.options"></SloderBar>
    </article>

    <article>
      <h4>圖片輪播</h4>
      <button @click="startComponent">啟用</button>
      <button @click="stopComponent">停用</button>
      
      <!-- 提醒這一層 div 一定要給一個 id (SlideShow 會認這個 id) -->
      <div class="testForSlideShow" id="testSlideShow">
        <SlideShow v-if="slideshow.isShow"
            :options="slideshow.options">
        </SlideShow>
      </div>
    </article>

    <article>
      <h4>Dialog</h4>
      dialog尺寸: 
      <select v-model="dialogs.size">
          <option value="S">S</option>
          <option value="M">M</option>
          <option value="L">L</option>
      </select> 
      <label>
          <input type="checkbox" v-model="dialogs.zoom" >
          <span>使用縮放功能</span>
      </label>
      <button @click="test_openDialog" >打開dialog</button>
      <Dialog :options="dialogs" >
          start <br>test <br>test <br>test <br>test <br>
          test <br>test <br>test <br>test <br>test <br>
          test <br>test <br>test <br>test <br>test <br>
          test <br>test <br>test <br>test <br>test <br>
          test <br>test <br>test <br>test <br>test <br>
          test <br>test <br>test <br>test <br>test <br>
          test <br>test <br>test <br>test <br>test <br>
          test <br>test <br>test <br>test <br>end <br>
      </Dialog>
    </article>

    <article>
      <h4>Pagination</h4>
      <button @click="test_forPageotions" >
        <span v-if="pageotions.totalcount === 100">假設從新更新頁碼資料用</span>
        <span v-if="pageotions.totalcount !== 100">頁碼套件恢復預設值</span>
      </button>
      <Pagination :options="pageotions"></Pagination>
    </article>

    <article>
      <h4>Pagination</h4>
      <Datepicker 
        :result.sync="dateOptions.dateModel"
        :placeholder="dateOptions.placeholder" 
        :lang="dateOptions.lang"
        :disabled="dateOptions.disabled"
        :keepshowPicker="dateOptions.keepshowPicker"
        :mindate="dateOptions.mindate"
        :maxdate="dateOptions.maxdate"
        :callback="dateOptions.callback"
        :useSlot="dateOptions.useSlot">
        <template slot="dateSlot" slot-scope="{ dateReturn, clickDate, checkDate }"> 
          <!-- 提醒: 套件限定用li -->
          <li v-for="(item, i) in dateReturn"
            @click="clickDate(item)" 
            :class="{
              'active': ((item.fullValue === dateOptions.dateModel) && (item.fullValue !== null)),
              'disable': checkDate(item)
            }" :key="i">
            
            <template v-if="item.fullValue">
              <span style="height: auto;">
                <i v-text="(item.dayTxt)? item.dayTxt : ''" style="font-size:11px; display: block; height: 13px; margin-left: -30%;"></i>
                <i v-text="mapData[(i/1)%2]" style="height: 15px; margin-left: 30%;"></i>
              </span>
            </template>
            <template v-else>
              <span style="height: auto;"></span>
            </template>

          </li>
        </template>
      </Datepicker>

      <Datepicker 
        :result.sync="dateModel2">
      </Datepicker>
    </article>

    <article>
      <h4>Scrollba</h4>
      scrollTop: {{scrollSettings.scrollTop}}
      <VuePerfectScrollbar class="test_scroll-area" 
        v-once 
        :settings="scrollSettings" 
        @ps-scroll-y="scrollSettings.psScroll_y">
        <p>
          The MIT License (MIT) Copyright (c) 2016 Hyunje Alex Jun and other contributors.

          Permission is hereby granted, free of charge, 
          to any person obtaining a copy of this software and associated documentation files (the "Software"), 
          to deal in the Software without restriction, including without limitation the rights to use, 
          copy, modify, merge, publish, distribute, sublicense, and/or sell copies of the Software, 
          and to permit persons to whom the Software is furnished to do so, 
          subject to the following conditions:

          The above copyright notice and this permission notice shall be included in all copies or substantial 
          portions of the Software.

          THE SOFTWARE IS PROVIDED "AS IS", WITHOUT WARRANTY OF ANY KIND, EXPRESS OR IMPLIED, INCLUDING BUT NOT 
          LIMITED TO THE WARRANTIES OF MERCHANTABILITY, FITNESS FOR A PARTICULAR PURPOSE AND NONINFRINGEMENT. 
          IN NO EVENT SHALL THE AUTHORS OR COPYRIGHT HOLDERS BE LIABLE FOR ANY CLAIM, DAMAGES OR OTHER LIABILITY, 
          WHETHER IN AN ACTION OF CONTRACT, TORT OR OTHERWISE, 
          ARISING FROM, OUT OF OR IN CONNECTION WITH THE SOFTWARE OR THE USE OR OTHER DEALINGS IN THE SOFTWARE.
        </p>
        <p>
          The MIT License (MIT) Copyright (c) 2016 Hyunje Alex Jun and other contributors.

          Permission is hereby granted, free of charge, 
          to any person obtaining a copy of this software and associated documentation files (the "Software"), 
          to deal in the Software without restriction, including without limitation the rights to use, 
          copy, modify, merge, publish, distribute, sublicense, and/or sell copies of the Software, 
          and to permit persons to whom the Software is furnished to do so, 
          subject to the following conditions:

          The above copyright notice and this permission notice shall be included in all copies or substantial 
          portions of the Software.

          THE SOFTWARE IS PROVIDED "AS IS", WITHOUT WARRANTY OF ANY KIND, EXPRESS OR IMPLIED, INCLUDING BUT NOT 
          LIMITED TO THE WARRANTIES OF MERCHANTABILITY, FITNESS FOR A PARTICULAR PURPOSE AND NONINFRINGEMENT. 
          IN NO EVENT SHALL THE AUTHORS OR COPYRIGHT HOLDERS BE LIABLE FOR ANY CLAIM, DAMAGES OR OTHER LIABILITY, 
          WHETHER IN AN ACTION OF CONTRACT, TORT OR OTHERWISE, 
          ARISING FROM, OUT OF OR IN CONNECTION WITH THE SOFTWARE OR THE USE OR OTHER DEALINGS IN THE SOFTWARE.
        </p>
        <p>
          The MIT License (MIT) Copyright (c) 2016 Hyunje Alex Jun and other contributors.

          Permission is hereby granted, free of charge, 
          to any person obtaining a copy of this software and associated documentation files (the "Software"), 
          to deal in the Software without restriction, including without limitation the rights to use, 
          copy, modify, merge, publish, distribute, sublicense, and/or sell copies of the Software, 
          and to permit persons to whom the Software is furnished to do so, 
          subject to the following conditions:

          The above copyright notice and this permission notice shall be included in all copies or substantial 
          portions of the Software.

          THE SOFTWARE IS PROVIDED "AS IS", WITHOUT WARRANTY OF ANY KIND, EXPRESS OR IMPLIED, INCLUDING BUT NOT 
          LIMITED TO THE WARRANTIES OF MERCHANTABILITY, FITNESS FOR A PARTICULAR PURPOSE AND NONINFRINGEMENT. 
          IN NO EVENT SHALL THE AUTHORS OR COPYRIGHT HOLDERS BE LIABLE FOR ANY CLAIM, DAMAGES OR OTHER LIABILITY, 
          WHETHER IN AN ACTION OF CONTRACT, TORT OR OTHERWISE, 
          ARISING FROM, OUT OF OR IN CONNECTION WITH THE SOFTWARE OR THE USE OR OTHER DEALINGS IN THE SOFTWARE.
        </p>
        <p>
          The MIT License (MIT) Copyright (c) 2016 Hyunje Alex Jun and other contributors.

          Permission is hereby granted, free of charge, 
          to any person obtaining a copy of this software and associated documentation files (the "Software"), 
          to deal in the Software without restriction, including without limitation the rights to use, 
          copy, modify, merge, publish, distribute, sublicense, and/or sell copies of the Software, 
          and to permit persons to whom the Software is furnished to do so, 
          subject to the following conditions:

          The above copyright notice and this permission notice shall be included in all copies or substantial 
          portions of the Software.

          THE SOFTWARE IS PROVIDED "AS IS", WITHOUT WARRANTY OF ANY KIND, EXPRESS OR IMPLIED, INCLUDING BUT NOT 
          LIMITED TO THE WARRANTIES OF MERCHANTABILITY, FITNESS FOR A PARTICULAR PURPOSE AND NONINFRINGEMENT. 
          IN NO EVENT SHALL THE AUTHORS OR COPYRIGHT HOLDERS BE LIABLE FOR ANY CLAIM, DAMAGES OR OTHER LIABILITY, 
          WHETHER IN AN ACTION OF CONTRACT, TORT OR OTHERWISE, 
          ARISING FROM, OUT OF OR IN CONNECTION WITH THE SOFTWARE OR THE USE OR OTHER DEALINGS IN THE SOFTWARE.
        </p>
      </VuePerfectScrollbar>
    </article>

    <article>
      <h4>directive: OnlyNum 限制輸入數字型別</h4>
      <input type="number" v-model="forOnlyNum" v-only-num="forOnlyNum" >
      {{forOnlyNum}}
    </article>

    <article>
      <h4>directive: ScrollChange 當 body scroll 到一定程度套用 class name</h4>
      <button @click="test_forScrollChange" >停/啟用</button>
      <div class="testForScrollChange"
        v-scroll-change="{
          class: 'in',
          range: [2000]
        }" v-if="forScrollChange">
          ScrollChange: 2000px
      </div>
      <div class="testForScrollChange"
        v-scroll-change="{
          class: 'in',
          range: [2000, 2400]
        }" v-if="forScrollChange">
          ScrollChange: 2000px ~ 2400px
      </div>
      <div class="testForScrollChange"
        v-scroll-change="{
          class: 'in',
          range: ['90%']
        }" v-if="forScrollChange">
          ScrollChange: 90%
      </div>
      <div class="testForScrollChange"
        v-scroll-change="{
          class: 'in',
          range: ['90%', '95%']
        }" v-if="forScrollChange">
          ScrollChange: 90%~ 95%
      </div>
    </article>

  </div>
</template>

<script>
import SloderBar from '../components/comp.sliderbar/';
import SlideShow from '../components/comp.slideshow/';
import Dialog from '../components/comp.dialog/';
import Pagination from '../components/comp.pagination/';
import Datepicker from '../components/comp.datepicker/';
import SwitchButton from '../components/comp.switch-button/';
import VuePerfectScrollbar from 'vue-perfect-scrollbar';

import { OnlyNum } from '../directives/OnlyNum.js';
import { ScrollChange } from '../directives/ScrollChange.js';

export default {
  name: 'Gui',
  components: { SloderBar, SlideShow, Dialog, Pagination, Datepicker, SwitchButton, VuePerfectScrollbar },
  directives: { OnlyNum, ScrollChange },
  data () {
    return {
      // 測試: switch button 用
      switchButton: {
        value: false,
        label: 'Power',
        callback: (val) => {
          console.log(val);
        }
      },
      // 測試: slideShow 用
      sliderBar: {
        isShow: true, //啡套件參數 for demo v-if 用
        options: {
          result: 20,
          minNum: 0,
          maxNum: 100,
          callback (result) {
            this.result = result;
            console.log('套件 sliderBar 回傳: ' + result);
          }
        }
      },
      // 測試: slideShow 用
      slideshow: {
        isShow: true,
        result_fn (link) {
          console.log('套件 slideshow 回傳: ' + link);
        },
        options: {
          parentId: 'testSlideShow',
          moveTime: 1000,
          startIndex: 2,
          autoPlayTime: 3500,
          debug: false,
          imgs: [
            {
              path: 'https://img.gohappy.com.tw/images/product/180/5403845/5403845_2.jpg',
              link: '#test01',
              click: (result) => {
                this.slideshow.result_fn(result.link);
              }
            }, {
              path: 'https://img.gohappy.com.tw/images/product/199/5975203/5975203_2.jpg',
              link: '#test02',
              click: (result) => {
                this.slideshow.result_fn(result.link);
              }
            }, {
              path: 'https://img.shopping.friday.tw/images/product/199/5997614/5997614_4.jpg',
              link: '#test03',
              click: (result) => {
                this.slideshow.result_fn(result.link);
              }
            }
          ]
        }
      },
      // 測試: dialog 用
      dialogs: {
        show: false,
        zoom: true,
        maskShow: true,
        zoomShow: false,
        size: 'S',
        closeCall () {
          console.log('Dialog is closed.');
        }
      },
      // 測試: 頁碼套件用
      pageotions: {
          debug: true,
          firsthide: true,
          lasthide: true,
          prevhide: true,
          nexthide: true,
          totalcount: 100,  // 資料總筆數
          pcount: 10,       // 一頁要顯示幾筆資料
          nowpage: 1,       // 現在在第幾頁
          pagenum: 5,       // 一次顯示幾顆頁碼
          pageclickfn (pagenum) { 
              this.nowpage = pagenum;
          }
      },
      // 測試: 小日曆用1
      dateOptions: {
        dateModel: '',
        placeholder: '請選擇日期',
        lang: 'en',
        disabled: false,
        keepshowPicker: false,
        mindate: '2018-08-22',
        maxdate: '2018-09-15',
        callback: this.datePickCallback,
        useSlot: true
      },
      mapData: ['○', '●'],  // 非小日曆套件參數但是提供 useSlot 的 template 呈現用
      // 測試: 小日曆用2
      dateModel2: '2018-08-25',
      // 測試: directive OnlyNum 用
      forOnlyNum: 100 ,
      // 測試: directive ScrollChange 用
      forScrollChange: true,
      // 測試: 使用別人寫好的套件 VuePerfectScrollbar
      scrollSettings: {
        maxScrollbarLength: 100,
        minScrollbarLength: 30,
        scrollTop: 0,
        psScroll_y: (event) => {
          this.scrollSettings.scrollTop = event.target.scrollTop;
        }
      }
    }
  },
  methods: {
    // [E] 啟用 slider bar 組件方法
    sliderBarStart () {
      this.sliderBar.isShow = true;
    }, 
    // [E] 停用 slider bar 組件方法
    sliderBarStop () {
      this.sliderBar.isShow = false;
    },
    // [E] 啟用 slideShow 組件方法
    startComponent () {
      this.slideshow.isShow = true;
    },
    // [E] 停用 slideShow 組件方法
    stopComponent () {
      this.slideshow.isShow = false;
    },
    // [E] 打開 dialog 方法
    test_openDialog () {
      this.dialogs.show = true;
    },
    // [E] 假設父層切換頁碼套件資料筆數的方法
    test_forPageotions () {
      this.pageotions.totalcount = (this.pageotions.totalcount === 100)? 20 : 100;
      this.pageotions.nowpage = 1;
    },
    // [E] 小日曆點擊後 callback
    datePickCallback () {
        console.log('小日曆點擊後 callback...');
    },
    // [E] 停用 directive ScrollChange 方法
    test_forScrollChange () {
      this.forScrollChange = !this.forScrollChange;
    },
    // [E] for ajax 用
    test_sendOne () {
      let url = '/data/opendata/datalist/apiAccess?scope=resourceAquire&rid=24c9f8fe-88db-4a6e-895c-498fbc94df94';
      this.rootHttpGet(url).then((response) => {
        console.log(response);
      });
    },
    // [E] for ajax 用
    test_sendMore () {
      let url = '/data/opendata/datalist/apiAccess?scope=resourceAquire&rid=24c9f8fe-88db-4a6e-895c-498fbc94df94';

      //假設在包成一個 function 
      let testFn = () => {
        return this.rootHttpGet('/data/youbike');
      };

      this.rootHttpAll([
        this.rootHttpGet(url),
        this.rootHttpGet('/data/youbike2'), //故意第二個用錯誤的 url
        testFn(),
        this.rootHttpGet(url),
        this.rootHttpGet('/data/youbike')
      ]).then((response) => {
        //疑惑: axios.all 是否有保鎮順序呢?
        console.log(response);
      });
    }
  }
}
</script>

<style lang="scss" scoped>
  @import '../scss/style.main.scss';
  
  // 測試用 區塊的距離
  article {
    padding: 0 $space $space*3 $space;

    h4 {
      margin-bottom: $space;
      padding-left: $space/2;
      font-size: $font-size-s;
      color: $grey-l;
      margin-left: -$space/2;
      border-left: 2px solid $blue-2;
    }
  }

  // 測試 圖片輪播 用
  .testForSlideShow {
    width: 50%; 
    padding: 0px;
  }

  // 測試 directive: ScrollChange 用
  .testForScrollChange {
    border: 1px solid $grey-l;
    padding: $space*2;
    margin: $space/2 0;
    text-align: center;
    color: $grey-l;
    transition: 0.4s;

    // 進入狀態
    &.in {
      border-width: $space/2;
      border-color: $grey-l;
      font-size: 200%;
      font-weight: 700;
      
    }
  }

  // 測試 使用別人寫好的套件 VuePerfectScrollbar 用
  .test_scroll-area {
    border: 1px solid $grey-ll;
    background-color: #fff;
    position: relative;
    margin: auto;
    width: 100%;
    height: 150px;
    padding: $space;
    box-sizing: border-box;
    line-height: 30px;
  }

  // RWD
  @media (max-width: $mobileWidth) {
    article {
      padding: 0 0 $space*3 0;
    }

    .testForSlideShow {
      width: 100%; 
    }
  }

</style>



